.ContactoView {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 132px);
    padding: 25px;
}

.Contacto-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: calc(100vh - 132px);
    max-width: 600px !important;
}

.ContactoView h2 {
    margin: 0;
    text-align: center;
    font-weight: 800;
    font-size: 32px;
}

.ContactoView p {
    text-align: center;
}

.ContactoView form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: 25px 0;
}

.ContactoView input, .ContactoView textarea {
    width: calc(100% - 32px);
    height: 25px;
    border-radius: 4px;
    border: 1px solid rgb(127, 127, 127);
    margin: 5px;
    padding: 10px;
}

.ContactoView input:focus, .ContactoView textarea:focus {
    outline-color: rgb(111, 111, 111);
}

.ContactoView textarea {
    resize: none;
    height: 100px;
}

.Contacto-submit {
    background-color: black;
    border: none;
    width: calc(100% - 10px) !important;
    height: 47px !important;
    color: white;
    font-weight: 500; 
    font-size: 18px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    margin: 5px;
}

.Contacto-submit:focus {
    outline: none;
}

.Contacto-redes-p {
    text-align: center;
    margin: 16px 10px;
}

@media(hover:hover) {
    .Contacto-submit:hover {
        opacity: 0.8;
    }
}