.Buscar {
    width: 20px;
    height: 20px;
    overflow: hidden;
    margin: 10px;
    padding: 5px 10px;
    border-radius: 5px;
    transition: 0.3s ease-in-out;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Buscar-input {
    border: none;
    background-color: transparent;
    width: calc(100% - 30px);
}

.Buscar-input:focus {
    outline: none;
}

.Buscar-button {
    width: 20px;
    height: 20px;
    border: none;
    cursor: pointer;
    margin-left: auto;
    transition: 0.2s ease-in-out;
}

.Buscar-button:focus {
    outline: none;
}

@media(hover:hover) {
    .Buscar-button:hover {
        opacity: 0.7;
    }
}

@media(max-width: 900px) {
    .Buscar {
        margin: 0 5px 5px 5px;
    }
}