.ComoComprarView {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 132px);
    padding: 25px;
}

.ComoComprar-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ComoComprarView h2 {
    margin: 0 0 16px 0;
    text-align: center;
    font-weight: 900;
    font-size: 32px;
}

.ComoComprar-steps {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 25px 0;
}

.ComoComprar-steps img {
    height: 30px;
    margin: auto 15px auto 15px;
}

.ComoComprar-step {
    width: 225px;
    display: flex;
}

.ComoComprar-step h4 {
    font-weight: 700;
    font-size: 60px;
    margin: 0;
}

.ComoComprar-step p {
    color: rgb(87, 86, 86);
    margin-top: 5px 0 25px 0;
}

.ComoComprar-redes-p {
    text-align: center;
    margin: 16px 10px;
}