.ListaProductosView {
    min-height: calc(100vh - 105px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-bottom: 25px;
}

.ListaProductos-lista {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.ListaProductos-noproductos {
    font-size: 24px;
    margin: 24px;
}

.ListaProductos-redes-p {
    text-align: center;
    margin: 16px 10px;
}