.ProductoView-mobile-carousel {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ProductoView-mobile-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.5);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}