#productoPicDisplay-container {
    overflow: hidden;
    display: flex;
}

#productoPicDisplay-big-img {
    width: 500px;
    height: 500px;
    margin: 5px;
    cursor: zoom-in;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

#productoPicDisplay-thumbs {
    display: flex;
    flex-direction: column;
}

.productoPicDisplay-thumb {
    width: 116px;
    height: 116px;
    margin: 5px;
    cursor: pointer;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.productoPicDisplay-thumb-plus {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255,255,255,0.6);
    color: rgba(0,0,0,0.6);
    margin-top: calc(-100% + 5px);
    font-size: 32px;
}

.productoPicDisplay-modal {
    width: 100%;
    height: 100vh;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;        
    background-color: rgba(0,0,0,0.85);
    z-index: 10;
    cursor: zoom-out;
}

@media (max-width: 850px) {
    .productoPicDisplay-thumb {
        width: 100%;
    }
}