.ProductoView {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: calc(100vh - 107px);
    padding-bottom: 25px;
}

.Producto-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 1180px;
}

.ProductoView-main {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    max-width: 1180px;
    margin: 25px;
}

.ProductoView-left {
    max-width: calc(50% - 28px);
    margin: 10px;
}

.ProductoView-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 25px;
    max-width: 400px;
    width: calc(50% - 28px);
}

.ProductoView-title {
    font-size: 32px;
    font-weight: 500;
    margin: 0;
}

.ProductoView-price {
    font-size: 28px;
    font-weight: 400;
    margin: 0;
}

.ProductoView-description-text {
    color: rgb(100,100,100);
}

.ProductoView-description-item {
    color: rgb(100,100,100);
    margin: 0;
}

.ProductoView-cart {
    display: flex;
    align-items: center;
    margin: 10px 0;
}

.ProductoView-cart-amount {
    border: 1px solid rgb(200,200,200);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 5px 2px 5px;
    margin-right: 5px;
}

.ProductoView-cart-amount p {
    margin: 0;
    margin-right: 15px;
}

.ProductoView-cart-amount-buttons {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ProductoView-cart-amount-buttons button {
    width: 12px;
    height: 12px;
    margin-top: 2px;
    border: none;
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: 70%;
    background-repeat: no-repeat;
    background-color: transparent;
    cursor: pointer;
}

.ProductoView-cart-amount-buttons button:focus {
    outline: none;
}

.ProductoView-cart-button {
    background-color: black;
    border: none;
    color: white;
    border-radius: 5px;
    font-size: 16px;
    padding: 6px 7px 7px 8px;
    margin: 5px;
    cursor: pointer;
    transition: 0.3s ease-in-out;
}

.ProductoView-cart-button:focus {
    outline: none;
}

.ProductoView-nostock {
    border: none;
    color: red;
    border-radius: 5px;
    font-size: 16px;
    padding: 3px 7px 5px 8px;
    text-align: center;
}

.ProductosView-redes-p {
    text-align: center;
    margin: 16px 10px;
}

@media(hover:hover) {
    .ProductoView-cart-button:hover {
        opacity: 0.8;
    }
}

@media(max-width: 900px) {
    .ProductoView-right {
        width: 100%;
        max-width: 100%;
    }
    .ProductoView-main {
        margin: 0;
    }
}