.ProductosRelacionados {
    width: calc(100% - 50px);
    margin: 0 25px 50px 25px;
}

.ProductosRelacionados-container {
    display: flex;
    align-items: center;
    max-width: 1180px;
    overflow: auto;
    padding: 0 10px;
}

.ProductosRelacionados-container::-webkit-scrollbar {
    height: 3px;
}

.ProductosRelacionados-container::-webkit-scrollbar-thumb {
    background: rgb(193, 193, 193);
}

.ProductoRelacionado {
    text-decoration: none;
    color: black;
    margin: 0 auto 10px auto;
}

.ProductoRelacionado-inner {
    width: 200px;
    height: 200px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

.ProductoRelacionado div {
    width: 150px;
    height: 150px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.ProductoRelacionado h4, .ProductoRelacionado p {
    margin: 0;
    font-size: 14px;
    font-weight: 500;
}

@media(hover:hover) {
    .ProductoRelacionado-inner:hover {
        opacity: 0.8;
    }
}