  .filter-container {
      text-align: left;
      margin: 8px 0 0 0;
      padding: 0 15px 10px 15px;
      background-color: rgb(236,236,236);
      z-index: 2;
      overflow: hidden;
      transition: 0.3s ease-in-out;
  }

  .filter-head {
    display: flex;
    cursor: pointer;
    transition: 0.2s ease-in-out;
  }

  .filter-title {
      margin: 0 10px 3px 0;
      font-size: 14px;
      font-weight: 400;
  }

  .filter-head img {
      width: 7px;
      margin-left: auto;
      transform: rotate(180deg)
  }

  .filter-buttons {
    display: flex;
    flex-direction: column;
    padding-top: 12px;
  }

  .filter-button {
      font-size: 13px;
      font-weight: 300;
      border: none;
      text-align: left;
      background-color: transparent;
      cursor: pointer;
      padding: 0;
      transition: 0.2s ease-in-out;
  }

  .filter-button:focus {
      outline: none;
  }

  .filter-button-inactive {
      cursor: default;
      opacity: 0.5;
  }

  .filter-checkbox {
      border: 1px solid rgba(0,0,0,0.4);
      border-radius: 3px;
      display: inline-block;
      width: 9px;
      height: 9px;
      margin-right: 3px;
      transform: translateY(1px);
      transition: 0.3s ease-in-out;
  }

  @media (hover: hover) {
      .filter-button:hover, .filter-head:hover {
          opacity: 0.8;
      }
      .filter-button-inactive:hover {
        opacity: 0.5;
    }
  }

  @media(max-width: 900px) {
    .filter-buttons {
      align-items: flex-start;
      margin-top: 0;
    }
    .filter-buttons {
      padding-top: 5px;
    }
  }