.PagoView {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 132px);
    padding: 25px;
}

.Pago-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 600px !important;
}

.PagoView h2 {
    margin: 0;
    text-align: center;
    font-weight: 800;
    font-size: 32px;
}

.Pago-buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.Pago-button {
    width: 175px;
    height: 28px;
    background-color: transparent;
    background-size: 50%;
    background-repeat: no-repeat;
    background-position: center;
    border: 1px solid rgb(200,200,200);
    border-radius: 5px;
    margin: 5px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 800;
    line-height: 14px;
    padding: 5px 0 10px 0;
    text-align: center;
    text-decoration: none;
    color: black;
}

.Pago-button span {
    font-size: 9px;
    line-height: 0px;
}

@media(max-width: 423px) {
    .Pago-buttons {
        margin-top: 25px;
    }
}