.Redes button {
    cursor: pointer;
    width: 40px;
    height: 40px;
    margin: 10px;
    background-size: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-color: black;
    border-radius: 50%;
    padding: 0;
    border: none;
    transition: 0.2s ease-in-out;
}

.Redes-fb {
    background-size: 25% !important;
}

.Redes button:focus {
    outline: none;
}

@media(hover: hover) {
    .Redes button:hover { 
        opacity: 0.8;
    }
}