.color-button {
  border: none;
  border-radius: 50%;
  box-shadow: inset 0px 0px 0px 1px rgba(0,0,0,0.3);
  cursor: pointer;
  margin: 0 10px 0 0;
  padding: 0;
  transition: opacity 0.3s ease-in-out;
}

.color-button:focus {
  outline: none;
}

@media(hover:hover) {
  .color-button:hover {
    opacity: 0.7;
  }
}