.NavBar {
    height: 360px;
    overflow: hidden;
    box-shadow: 0 2px 2px -2px rgba(0,0,0,.2);
    display: flex;
    justify-content: center;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    background-color: white;
    z-index: 5;
    transition: 0.3s ease-in-out;
}

.NavBar-content {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 1180px;
}

.NavBar-logo {
    height: 30px;
    margin-left: 10px;
}

.NavBar-logo:focus {
    outline: none;
}

.NavBar-right {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    flex: 1 1;
}

.NavBar-buscar {
    margin: 20px;
}

.NavBar-burguer-button {
    width: 20px;
    height: 20px;
    margin: 20px 20px 20px auto;
    background-color: transparent;
    border: none;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
}

.NavBar-burguer-button:focus {
    outline: none;
}

.NavBar-buttons {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex: 1 1;
}

.NavBar-button {
    text-decoration: none;
    margin: 20px;
    color: black;
    font-size: 17px;
    transition: 0.2s ease-in-out;
}

.NavBar-button:focus {
    outline: none;
}

.NavBar-log {
    color: white;
    background-color: black;
    padding: 5px 10px;
}


@media(hover:hover) {
    .NavBar-button:hover {
        opacity: 0.7;
    }
}

@media(max-width:900px) {
    .NavBar {
        align-items: flex-start;
    }
    .NavBar-content {
        align-items: start;
        flex-direction: column;
    }
    .NavBar-left {
        width: 100%;
        display: flex;
        height: 80px;
        align-items: center;
    }
    .NavBar-logo {
        margin-bottom: 2px;
    }
    .NavBar-right {
        flex-direction: column;
        margin: auto;
    }
    .NavBar-buttons {
        flex-direction: column;
        background-color: white;
        align-items: center;
        width: 100%;
    }
    .NavBar-button {
        margin: 10px;
    }
}
.Buscar {
