#filter-list-container {
  width: 100%;
  background-color: rgb(236,236,236);
  margin: 0;
  display: flex;
  height: 36px;
  overflow: visible;
  justify-content: center;
  transition: 0.4s ease-in-out
}

.filter-list-mobile-button {
  border: none;
  height: 100%;
  margin: 10px 0;
  cursor: pointer;
  transition: 0.2s ease-in-out;
  background-color: transparent;
}

.filter-list-mobile-button:focus {
  outline: none;
}

.filter-list-inner {
  display: flex;
  height: 36px;
  overflow: visible;
  width: 100%;
  max-width: 1180px;
  padding-left: 8px;
  transition: 0.4s ease-in-out
}

.filter-list-search {
  color: black;
  cursor: pointer;
  border: 1px solid rgba(0,0,0,0.5);
  border-radius: 4px;
  margin: 5px 20px 5px auto;
  padding: 4px 10px 5px 10px;
  text-decoration: none;
  font-size: 13px;
  transition: 0.2s ease-in-out;
}

.filter-list-search:focus {
  outline: none;
}

.filter-list-search img {
    margin: 0 0 1px 7px;
    width: 7px;
}

@media(hover:hover) {
  .filter-list-search:hover {
    opacity: 0.8;
  }
  .filter-list-mobile-button:hover {
    opacity: 0.8;
  }
}

@media(max-width: 900px) {
  #filter-list-container {
    flex-direction: column;
    justify-content: flex-start;
    height: auto;
  }
  .filter-list-inner {
    height: auto;
    overflow: hidden;
    flex-direction: column;
  }
  .filter-list-search {
    display: none;
  }
}
  .filter-container {
      text-align: left;
      margin: 8px 0 0 0;
      padding: 0 15px 10px 15px;
      background-color: rgb(236,236,236);
      z-index: 2;
      overflow: hidden;
      transition: 0.3s ease-in-out;
  }

  .filter-head {
    display: flex;
    cursor: pointer;
    transition: 0.2s ease-in-out;
  }

  .filter-title {
      margin: 0 10px 3px 0;
      font-size: 14px;
      font-weight: 400;
  }

  .filter-head img {
      width: 7px;
      margin-left: auto;
      transform: rotate(180deg)
  }

  .filter-buttons {
    display: flex;
    flex-direction: column;
    padding-top: 12px;
  }

  .filter-button {
      font-size: 13px;
      font-weight: 300;
      border: none;
      text-align: left;
      background-color: transparent;
      cursor: pointer;
      padding: 0;
      transition: 0.2s ease-in-out;
  }

  .filter-button:focus {
      outline: none;
  }

  .filter-button-inactive {
      cursor: default;
      opacity: 0.5;
  }

  .filter-checkbox {
      border: 1px solid rgba(0,0,0,0.4);
      border-radius: 3px;
      display: inline-block;
      width: 9px;
      height: 9px;
      margin-right: 3px;
      transform: translateY(1px);
      transition: 0.3s ease-in-out;
  }

  @media (hover: hover) {
      .filter-button:hover, .filter-head:hover {
          opacity: 0.8;
      }
      .filter-button-inactive:hover {
        opacity: 0.5;
    }
  }

  @media(max-width: 900px) {
    .filter-buttons {
      align-items: flex-start;
      margin-top: 0;
    }
    .filter-buttons {
      padding-top: 5px;
    }
  }
