.carousel-container {
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-self: center;
    position: relative;
    overflow: hidden;
}

#carousel-content-container {
    display: flex;
    justify-content: stretch;
    transition: 0.5s ease-in-out;
    width: 100%;
    height: 100%;
}

.carousel-element {
    align-self: center;
    background-repeat: no-repeat !important;
    background-size: contain;
    background-position: center;
    transition: 0.3s ease-in-out;
    flex: 0 0 100%;
    height: 100%;
}

#carousel-circles-container {
    position: absolute;
    bottom: 15px;
    left: calc(50% - 52px);
    text-align: center;
}

.carousel-circle-button {
    width: 18px;
    height: 18px;
    margin: 5px;
