.ConfirmacionCompraView {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 132px);
    padding: 25px;
}

.ConfirmacionCompra-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 450px !important;
}

.ConfirmacionCompra h2 {
    margin: 0;
    text-align: center;
    font-weight: 800;
    font-size: 32px;
}

.ConfirmacionCompra-productos {
    width: 100%;
    margin: 20px 10px 10px 10px;
    color: rgb(87, 87, 87);
}

.ConfirmacionCompra-productos-elements {
    border-bottom: 1px solid rgba(87, 87, 87, 0.8);
}

.ConfirmacionCompra-producto {
    display: flex;
    font-size: 15px;
}

.ConfirmacionCompra-producto-nombre {
    flex: 1;
    margin: 3px;
}

.ConfirmacionCompra-producto-precio {
    margin: 3px;
    text-align: right;
}

.ConfirmacionCompra-productos-bottom {
    display: flex;
}

.ConfirmacionCompra-productos-agregar {
    margin: 3px;
    text-decoration: none;
    color: white;
    background-color: rgb(87, 87, 87);
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 24px;
    width: 125px;
    font-size: 12px;
}

.ConfirmacionCompra-productos-total {
    flex: 1;
    margin: 3px;
    text-align: right;
    font-size: 15px;
}

.ConfirmacionCompra-datos {
    width: 100%;
    margin: 10px;
}

.ConfirmacionCompra-datos p {
    margin: 5px;
    color: rgb(87, 87, 87);
    font-size: 15px;
}

.ConfirmacionCompra-datos-editar {
    margin: 3px;
    text-decoration: none;
    color: white;
    background-color: rgb(87, 87, 87);
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 125px;
    height: 24px;
    font-size: 12px;
}

.ConfirmacionCompra-metodoDePago {
    width: 100%;
}

.ConfirmacionCompra-metodoDePago p {
    margin: 5px;
    color: rgb(87, 87, 87);
    font-size: 15px;
}

.ConfirmacionCompra-submit {
    background-color: black;
    border: none;
    border-radius: 4px;
    width: 100%;
    height: 47px !important;
    color: white;
    font-weight: 500; 
    font-size: 18px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 25px 10px 10px 10px;
}

.ConfirmacionCompra-submit:focus {
    outline: none;
}

@media(hover:hover) {
    .ConfirmacionCompra-submit:hover {
        opacity: 0.8;
    }
}