#filter-list-container {
  width: 100%;
  background-color: rgb(236,236,236);
  margin: 0;
  display: flex;
  height: 36px;
  overflow: visible;
  justify-content: center;
  transition: 0.4s ease-in-out
}

.filter-list-mobile-button {
  border: none;
  height: 100%;
  margin: 10px 0;
  cursor: pointer;
  transition: 0.2s ease-in-out;
  background-color: transparent;
}

.filter-list-mobile-button:focus {
  outline: none;
}

.filter-list-inner {
  display: flex;
  height: 36px;
  overflow: visible;
  width: 100%;
  max-width: 1180px;
  padding-left: 8px;
  transition: 0.4s ease-in-out
}

.filter-list-search {
  color: black;
  cursor: pointer;
  border: 1px solid rgba(0,0,0,0.5);
  border-radius: 4px;
  margin: 5px 20px 5px auto;
  padding: 4px 10px 5px 10px;
  text-decoration: none;
  font-size: 13px;
  transition: 0.2s ease-in-out;
}

.filter-list-search:focus {
  outline: none;
}

.filter-list-search img {
    margin: 0 0 1px 7px;
    width: 7px;
}

@media(hover:hover) {
  .filter-list-search:hover {
    opacity: 0.8;
  }
  .filter-list-mobile-button:hover {
    opacity: 0.8;
  }
}

@media(max-width: 900px) {
  #filter-list-container {
    flex-direction: column;
    justify-content: flex-start;
    height: auto;
  }
  .filter-list-inner {
    height: auto;
    overflow: hidden;
    flex-direction: column;
  }
  .filter-list-search {
    display: none;
  }
}