#pagination-container {
    margin: auto;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.pagination-button {
    margin: 25px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 0; 
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
}

.pagination-button img {
    height: 13px;
    margin: 0 10px;
}

.pagination-button-disabled {
    cursor: default;
    opacity: 0.5;
}

.pagination-button:focus {
    outline: none;
}

#pagination-text {
    font-size: 16px;
    font-weight: 500;
}