.carousel-container {
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-self: center;
    position: relative;
    overflow: hidden;
}

#carousel-content-container {
    display: flex;
    justify-content: stretch;
    transition: 0.5s ease-in-out;
    width: 100%;
    height: 100%;
}

.carousel-element {
    align-self: center;
    background-repeat: no-repeat !important;
    background-size: contain;
    background-position: center;
    transition: 0.3s ease-in-out;
    flex: 0 0 100%;
    height: 100%;
}

#carousel-circles-container {
    position: absolute;
    bottom: 15px;
    left: calc(50% - 52px);
    text-align: center;
}

.carousel-circle-button {
    width: 18px;
    height: 18px;
    margin: 5px;
    padding: 0;
    border: 1px solid rgba(0,0,0,0.5);
    border-radius: 50%;
    cursor: pointer;
    background-color: rgba(255,255,255,0.3);
    transition: 0.3s ease-in-out;
}

.carousel-circle-button-active {
    border: 1px solid rgba(0,0,0,1);
    background-color: rgba(255,255,255,0.7);
}

.carousel-circle-button:focus {
    outline: none;
}

#carousel-arrows-container {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(111,111,111);
    font-weight: 200;
    cursor: default;
}

.carousel-arrow-button {
    margin: 12px 10px 10px 10px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 0; 
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    transition: 0.3s ease-in-out;
    filter: invert(44%)
}

.carousel-arrow-button img {
    height: 13px;
    margin: 0 10px;
}

.carousel-arrow-button:focus {
    outline: none;
}

@media(hover:hover) {
    .carousel-arrow-button:hover {
        opacity: 1;
    }
    .carousel-circle-button:hover {
        opacity: 0.5;
    }
}

@media(max-width: 850px) {
    .carousel-container {
        width: 100%;
        margin: 0;
    }
}