.BurbujaCarrito {
    position: fixed;
    height: calc(100vh - 80px);
    top: 80px;
    right: 0;
}

.BurbujaCarrito-button {
    width: 50px;
    height: 50px;
    bottom: 25px;
    right: 25px;
    background-color: white;
    background-repeat: no-repeat;
    background-position: 55% 55%;
    background-size: 55%;
    box-shadow: 0 7px 16px 0 rgba(255, 255, 255, .4), 0 1px 3px 0 rgba(255, 255, 255, .2);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    position: fixed;
    filter: invert(100%);
}

.BurbujaCarrito-button-cantidad {
    background-color: rgb(0, 255, 255);
    border-radius: 50%;
    width: 19px;
    height: 20px;
    font-size: 13px;
    font-weight: 800;
    line-height: 20px;
    text-align: center;
    padding-right: 1px;
    position: absolute;
    right: -3px;
    top: -15px;
}

@media(hover:hover) {
    .BurbujaCarrito-button:hover {
        opacity: 0.8;
    }
}