.CompraErroneaView {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 132px);
    padding: 25px;
}

.CompraErronea-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 600px !important;
}

.CompraErroneaView h2 {
    margin: 0;
    text-align: center;
    font-weight: 800;
    font-size: 32px;
}

.CompraErronea-mensajes {
    margin: 10px 0;
}

.CompraErronea-mensaje {
    margin: 10px;
    text-align: center;
}

.CompraErronea-volver {
    background-color: black;
    border: none;
    width: 100%;
    max-width: 350px;
    height: 47px !important;
    border-radius: 4px;
    color: white;
    font-weight: 500; 
    font-size: 18px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}

.CompraErronea-volver:focus {
    outline: none;
}

@media(hover:hover) {
    .CompraErronea-volver:hover {
        opacity: 0.8;
    }
}