.QuienRecibeView {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 132px);
    padding: 25px;
}

.QuienRecibe-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 600px !important;
}

.QuienRecibeView h2 {
    margin: 0;
    text-align: center;
    font-weight: 800;
    font-size: 32px;
}

.QuienRecibeView form {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.QuienRecibe-input-container {
    display: flex;
    flex-direction: column;
    width: calc(50% - 30px);
    min-width: 270px;
    margin: 0 15px;
}

.QuienRecibeView form label {
    margin: 15px auto 0 15px;
    font-size: 14px;
}

.QuienRecibeView input {
    height: 25px;
    border-radius: 4px;
    border: 1px solid rgb(127, 127, 127);
    margin: 0;
    padding: 10px;
}

.QuienRecibeView input:focus {
    outline-color: rgb(111, 111, 111);
}

.QuienRecibe-miniinput-container {
    width: calc(50% - 30px);
    min-width: 270px;
    margin: 0 15px;
    display: flex;
    justify-content: space-between;
}

.QuienRecibe-miniinput {
    width: 30%;
    margin: 15px 0 0 0;
}

.QuienRecibe-miniinput input {
    width: calc(100% - 20px);
}

.QuienRecibe-mediuminput-container {
    width: calc(50% - 30px);
    min-width: 270px;
    margin: 0 15px;
    display: flex;
    justify-content: space-between;
}

.QuienRecibe-codigopostal {
    width: calc(50% - 7px);
    margin-top: 15px;
}

.QuienRecibe-provincia {
    width: calc(50% - 7px);
    margin-top: 15px;
}

.QuienRecibe-codigopostal input, .QuienRecibe-provincia input{
    width: calc(100% - 20px);
}
.QuienRecibe-newsletter {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    height: 47px;
    margin: 15px;
    min-width: 270px;
}

.QuienRecibe-newsletter div {
    width: 20px;
    height: 20px;
    border-radius: 4px;
    border: 1px solid rgb(127, 127, 127);
    cursor: pointer;
    padding: 0;
    margin: 0;
    transition: 0.2s ease-in-out;
    background-size: 80%;
    background-position: 50% 55%;
    background-repeat: no-repeat;
}

.QuienRecibe-newsletter label {
    width: calc(100% - 30px);
    margin: 0 0 0 10px !important;
}

.QuienRecibe-link {
    width: calc(50% - 30px);
    min-width: 270px;
    margin: 0 15px;
    padding: 0 !important;
    align-self: flex-end;
}

.QuienRecibe-submit {
    background-color: black;
    border: none;
    width: 100%;
    height: 47px !important;
    color: white;
    font-weight: 500;
    font-size: 18px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

.QuienRecibe-link:focus, .QuienRecibe-submit:focus {
    outline: none;
}

@media(hover:hover) {
    .QuienRecibe-submit:hover {
        opacity: 0.8;
    }
}