.product-list-element {
    width: 300px;
    height: 280px;
    margin: 5px;
    padding: 15px;
    text-align: center;
    display: flex;
    flex-direction: column;
    background-color: white;
    transition: box-shadow 0.5s ease-in-out, transform 0.5s ease-in-out, z-index 1s ease-in-out;
}

.product-list-element-link {
    color: black;
    text-decoration: none;
    width: calc(100%);
    height: calc(100%);
}

.product-list-element-link:focus {
    outline: none;
}

.product-list-element-img {
    width: calc(100%);
    height: calc(100%);
    background-repeat: no-repeat !important;
    background-size: contain !important;
    background-position: center !important;
    cursor: pointer;
    margin: auto;
    transition: 0.3s ease-in-out
}

.product-list-element-text {
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.product-list-element-title {
    font-size: 14px;
    font-weight: 500;
    margin: 0;
    cursor: pointer;
    transition: 0.3s ease-in-out;
}

.product-list-element-nostock {
    color: red;
    margin: 0;
    font-size: 14px;
}

.product-list-element-price {
    font-size: 14px;
    margin: 0;
}

@media (hover: hover) {
    .product-list-element-title:hover {
        opacity: 0.7;
    }
}