.CompraExitosaView {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 132px);
    padding: 25px;
}

.CompraExitosa-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 600px !important;
}

.CompraExitosaView h2 {
    margin: 0;
    text-align: center;
    font-weight: 800;
    font-size: 32px;
}

.CompraExitosa-mensaje {
    margin: 10px 0;
}

.CompraExitosa-transferencia-datos {
    background-color: rgba(255, 246, 197,0.3);
    border-radius: 4px;
    padding: 5px;
}

.CompraExitosa-transferencia-datos p {
    margin: 5px;
}

.CompraExitosa-volver {
    background-color: black;
    border: none;
    width: 100%;
    max-width: 350px;
    height: 47px !important;
    border-radius: 4px;
    color: white;
    font-weight: 500; 
    font-size: 18px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}

.CompraExitosa-volver:focus {
    outline: none;
}

@media(hover:hover) {
    .CompraExitosa-volver:hover {
        opacity: 0.8;
    }
}