.Carrito {
    position: absolute;
    border-top: 5px solid black;
    width: 412px;
    max-width: calc(100vw + 4px);
    height: calc(100vh - 78px);
    background-color: white;
    overflow-y: auto;
    overflow-x: hidden;
    transition: 0.3s ease-in-out;
    box-shadow: 0 0 2px 0px rgba(0,0,0,.2);
}

.Carrito::-webkit-scrollbar {
    width: 4px;
}

.Carrito::-webkit-scrollbar-thumb {
    background: rgb(193, 193, 193);
}

.Carrito-titulo-contenedor {
    margin: 10px 15px 0 15px;
    padding: 0 5px 10px 5px;
    border-bottom: 1px solid rgba(206, 206, 206, 1);
    display: flex;
    align-items: center;
}

.Carrito-titulo {
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
}

.Carrito-cerrar {
    margin: 0 0 0 auto;
    border: none;
    width: 12px;
    height: 12px;
    cursor: pointer;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-color: white;
}

.Carrito-cerrar:focus {
    outline: none;
}

.Carrito-producto {
    display: flex;
    align-items: center;
    height: 60px;
    padding: 5px;
    overflow: hidden;
}

.Carrito-producto-main {
    display: flex;
    align-items: center;
    flex: 1;
    padding: 0 5px;
}

.Carrito-producto-imagen {
    height: 50px;
    width: 50px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 75%;
}

.Carrito-producto-main p {
    flex: 1;
    font-weight: 500;
    font-size: 14px;
}

.Carrito-producto-precio {
    border-left: 1px solid rgb(100, 100, 100);
    border-right: 1px solid rgb(100, 100, 100);
    padding: 0 15px;
    font-weight: 400;
}

.Carrito-producto-cantidad {
    display: flex;
    align-items: center;
    padding: 0 15px;
    border-right: 1px solid rgb(100, 100, 100);
    height: 24px;
}

.Carrito-producto-cantidad button {
    border: none;
    border-radius: 3px;
    height: 13px;
    width: 13px;
    padding: 0;
    margin: 0;
    cursor: pointer;
    background-size: contain;
    background-color: white;
}

.Carrito-producto-cantidad button:focus {
    outline: none;
}

.Carrito-producto-cantidad p {
    color: rgb(100, 100, 100);
    font-weight: 400;
    width: 20px;
    text-align: center;
    margin: 5px;
}

.Carrito-producto-eliminar {
    border: none;
    width: 17px;
    height: 17px;
    cursor: pointer;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-color: transparent;
    margin: 0 15px;
}

.Carrito-producto-eliminar:focus {
    outline: none;
}

.Carrito-checkout {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 25px;
    text-decoration: none;
}

.Carrito-checkout button {
    background-color: black;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    padding: 3px 7px 5px 8px;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    margin-bottom: 25px;
}

.Carrito-checkout button:focus {
    outline: none;
}

@media(hover:hover) {
    .Carrito-cerrar:hover {
        opacity: 0.7;
    }
    .Carrito-checkout button:hover {
        opacity: 0.8;
    }
}

@media(max-width: 400px) {
    .Carrito-producto-main {
        padding: 0 5px;
    }
    .Carrito-producto-main p, .Carrito-producto-precio, .Carrito-producto-cantidad p {
        font-size: 12px;
    }
    .Carrito-producto-cantidad p {
        margin: 0;
    }
}